
.c-feedback {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.c-feedback .c-feedback-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 720px;
  padding: 60px 0 80px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #2d2d2d;
}
.c-feedback .content-form {
  width: 330px;
  height: 230px;
  padding: 22px 30px;
  margin: 0 auto;
  margin-bottom: 20px;
}
.c-feedback .content-form textarea {
  color: #505050;
  font-size: 16px;
}
.c-feedback .content-input {
  width: 330px;
  height: 60px;
  padding: 20px 30px;
  margin: 0 auto;
  margin-bottom: 20px;
}
.c-feedback .content-input input {
  color: #505050;
  font-size: 16px;
}
.c-feedback .content-btn {
  width: 330px;
  height: 60px;
  overflow: hidden;
  margin: 0 auto;
}
.c-feedback .content-btn .content-btn-cancel {
  width: 80px;
  height: 60px;
  line-height: 60px;
  float: left;
}
.c-feedback .content-btn .content-btn-submit {
  width: 240px;
  height: 60px;
  line-height: 60px;
  float: right;
}
.c-feedback .c-dialog__tip {
  -webkit-transform: translate(-140%, 0);
          transform: translate(-140%, 0);
}



