
.c-footer {
  width: 100%;
  height: 100px;
  background-color: #181818;
}
.c-footer-content {
  height: 100%;
  margin: 0 auto;
  width: 960px;
}
.c-footer-content .menu {
  float: left;
  padding-top: 43px;
  color: #868686;
}
.c-footer-content .menu a {
  font-size: 14px;
  color: #868686;
}
.c-footer-content .icon {
  float: right;
  margin-top: 30px;
}
.c-footer-content .icon .icon-item {
  display: block;
  float: left;
  width: 40px;
  height: 40px;
  margin-left: 20px;
  border-radius: 50%;
  background-color: #282828;
}
.c-footer-content .icon .icon-item:nth-child(1) {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/ios01.png');
}
.c-footer-content .icon .icon-item:nth-child(1):hover {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/ios02.png');
}
.c-footer-content .icon .icon-item:nth-child(2) {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/android01.png');
}
.c-footer-content .icon .icon-item:nth-child(2):hover {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/android02.png');
}



