
.c-header {
  width: 100%;
  height: 150px;
  background-color: #181818;
}
.c-header-content {
  width: 960px;
  height: 100%;
  margin: 0 auto;
}
.c-header-content .logo {
  float: left;
  width: 140px;
  height: 50px;
  margin-top: 50px;
  margin-right: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/logo.png');
  cursor: pointer;
}
.c-header-content .logo a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.c-header-content .menu {
  float: left;
  margin-top: 78px;
  margin-right: 30px;
  overflow-y: hidden;
}
.c-header-content .menu .item {
  float: left;
  margin-right: 20px;
  font-size: 16px;
  color: #868686;
  text-decoration: none;
}
.c-header-content .menu .item:hover {
  color: #e1e1e1;
}
.c-header-content .menu .item.z-on {
  color: #e1e1e1;
  font-weight: bolder;
}
.c-header-content .search {
  float: left;
  width: 250px;
  height: 40px;
  overflow: hidden;
  padding: 10px 20px;
  margin-top: 68px;
  border-radius: 4px;
  background-color: #282828;
}
.c-header-content .search input {
  display: block;
  float: left;
  width: 170px;
  margin-top: 3px;
  margin-right: 15px;
  color: #505050;
  font-size: 16px;
}
.c-header-content .search a {
  display: block;
  float: left;
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/search.png');
  cursor: pointer;
}
.c-header-content .user {
  float: right;
  margin-right: 20px;
  margin-top: 70px;
  text-align: center;
}
.c-header-content .user .btn {
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  color: #e1e1e1;
  font-weight: bolder;
}
.c-header-content .user .btn a {
  color: #e1e1e1;
}
.c-header-content .user .member {
  display: none;
  position: relative;
  width: 100px;
}
.c-header-content .user .member .member-show-item {
  width: 40px;
  height: 40px;
  float: left;
  cursor: pointer;
  border-radius: 50%;
  background-color: #373737;
}
.c-header-content .user .member .member-show-notice {
  position: relative;
  margin-right: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/notice.png');
}
.c-header-content .user .member .member-show-notice .member-show-notice_number {
  position: absolute;
  top: -4px;
  right: -15px;
}
.c-header-content .user .member .member-show-center {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.c-header-content .user .member .member-notice {
  position: absolute;
  top: 77px;
  left: 0;
  width: 200px;
  z-index: 10;
  overflow: hidden;
  background-color: #2d2d2d;
  border-radius: 4px;
}
.c-header-content .user .member .member-notice .member-notice-item {
  width: 200px;
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
}
.c-header-content .user .member .member-notice .member-notice-item a {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 16px;
  color: #868686;
}
.c-header-content .user .member .member-notice .member-notice-item .member-notice-item__list {
  display: block;
  float: left;
}
.c-header-content .user .member .member-notice .member-notice-item .member-notice-item__icon {
  width: 20px;
  height: 100%;
  margin-right: 10px;
}
.c-header-content .user .member .member-notice .member-notice-item .member-notice-item__text {
  margin-top: 22px;
}
.c-header-content .user .member .member-notice .member-notice-item .member-notice-item__number {
  float: left;
  margin-top: 22px;
  margin-right: 10px;
}
.c-header-content .user .member .member-notice .member-notice-item:nth-child(1) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/er.png');
}
.c-header-content .user .member .member-notice .member-notice-item:nth-child(2) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/cmt.png');
}
.c-header-content .user .member .member-notice .member-notice-item:nth-child(3) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/msg.png');
}
.c-header-content .user .member .member-notice .member-notice-item:hover {
  background-color: #181818;
}
.c-header-content .user .member .member-notice .member-notice-item:hover a {
  color: #e1e1e1;
}
.c-header-content .user .member .member-notice .member-notice-item:hover:nth-child(1) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/er_on.png');
}
.c-header-content .user .member .member-notice .member-notice-item:hover:nth-child(2) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/cmt_on.png');
}
.c-header-content .user .member .member-notice .member-notice-item:hover:nth-child(3) .member-notice-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/msg_on.png');
}
.c-header-content .user .member .member-center {
  position: absolute;
  top: 77px;
  left: 60px;
  width: 200px;
  z-index: 10;
  overflow: hidden;
  background-color: #2d2d2d;
  border-radius: 4px;
  cursor: pointer;
}
.c-header-content .user .member .member-center .member-center-item {
  width: 200px;
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
  color: #868686;
}
.c-header-content .user .member .member-center .member-center-item a {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 16px;
  color: #868686;
}
.c-header-content .user .member .member-center .member-center-item .member-center-item__list {
  display: block;
  float: left;
}
.c-header-content .user .member .member-center .member-center-item .member-center-item__icon {
  width: 20px;
  height: 100%;
  margin-right: 10px;
}
.c-header-content .user .member .member-center .member-center-item .member-center-item__text {
  margin-top: 22px;
}
.c-header-content .user .member .member-center .member-center-item:nth-child(1) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/me.png');
}
.c-header-content .user .member .member-center .member-center-item:nth-child(2) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/set.png');
}
.c-header-content .user .member .member-center .member-center-item:nth-child(3) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/back.png');
}
.c-header-content .user .member .member-center .member-center-item:nth-child(4) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/logout.png');
}
.c-header-content .user .member .member-center .member-center-item:hover {
  background-color: #181818;
  color: #e1e1e1;
}
.c-header-content .user .member .member-center .member-center-item:hover a {
  color: #e1e1e1;
}
.c-header-content .user .member .member-center .member-center-item:hover:nth-child(1) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/me_on.png');
}
.c-header-content .user .member .member-center .member-center-item:hover:nth-child(2) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/set_on.png');
}
.c-header-content .user .member .member-center .member-center-item:hover:nth-child(3) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/back_on.png');
}
.c-header-content .user .member .member-center .member-center-item:hover:nth-child(4) .member-center-item__icon {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/logout_on.png');
}
.c-header-content .user.z-active .btn {
  display: none;
}
.c-header-content .user.z-active .member {
  display: block;
}
.c-header-content .publish {
  float: right;
  margin-top: 70px;
  background-color: #2d2d2d;
  font-weight: bolder;
}



