
.c-author-list {
  width: 100%;
  height: 100px;
  padding: 0 60px;
}
.c-author-list .c-author-list-wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-bottom: 1px solid #1e1e1e;
}
.c-author-list .list-left {
  float: left;
  width: 200px;
  height: 40px;
  overflow: hidden;
  margin-top: 30px;
}
.c-author-list .list-left .img {
  float: left;
  width: 40px;
  height: 40px;
  margin-right: 16px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #282828;
  border-radius: 50%;
  overflow: hidden;
}
.c-author-list .list-left .name {
  float: left;
  padding-top: 10px;
  font-size: 20px;
  color: #aeaeae;
}
.c-author-list .list-right {
  float: right;
  width: 100px;
  height: 40px;
  line-height: 40px;
  margin-right: 60px;
  margin-top: 30px;
}
.c-author-list .list-right .list-right-item {
  width: 100px;
}



