
.c-edit {
  width: 100%;
  border-radius: 4px;
  background-color: #282828;
  overflow: hidden;
}
.c-edit .edit-title {
  width: 100%;
  height: 80px;
  padding-top: 30px;
  padding-left: 40px;
}
.c-edit .edit-title .edit-title-t {
  float: left;
  margin-right: 5px;
  color: #868686;
  font-size: 20px;
  font-weight: bolder;
}
.c-edit .edit-title .edit-title-h {
  float: left;
}
.c-edit .edit-title .edit-title-h input {
  color: #505050;
  font-size: 20px;
}
.c-edit .edit-tool {
  position: relative;
  width: 100%;
  height: 60px;
  background-color: #373737;
  border-top: 1px solid #232323;
  border-bottom: 1px solid #232323;
}
.c-edit .edit-tool .edit-tool-item {
  position: relative;
  float: left;
  width: 50px;
  height: 58px;
  text-align: center;
  cursor: pointer;
}
.c-edit .edit-tool .edit-tool-item a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.c-edit .edit-tool .edit-tool-item em {
  display: inline-block;
  width: 100%;
  height: 100%;
  vertical-align: middle;
}
.c-edit .edit-tool .edit-tool-item:hover {
  background-color: #181818;
}
.c-edit .edit-tool .edit-tool-item:hover .edit-tool-tip {
  display: block;
}
.c-edit .edit-tool .edit-tool-suo {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_suo.png');
}
.c-edit .edit-tool .edit-tool-suo:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_suo_on.png');
}
.c-edit .edit-tool .edit-tool-bold {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_bold.png');
}
.c-edit .edit-tool .edit-tool-bold:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_bold_on.png');
}
.c-edit .edit-tool .edit-tool-xie {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_xie.png');
}
.c-edit .edit-tool .edit-tool-xie:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_xie_on.png');
}
.c-edit .edit-tool .edit-tool-delete {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_delete.png');
}
.c-edit .edit-tool .edit-tool-delete:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_delete_on.png');
}
.c-edit .edit-tool .edit-tool-img {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_img.png');
}
.c-edit .edit-tool .edit-tool-img:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_img_on.png');
}
.c-edit .edit-tool .edit-tool-save {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_save.png');
}
.c-edit .edit-tool .edit-tool-save:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_save_on.png');
}
.c-edit .edit-tool .edit-tool-scan {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_scan.png');
}
.c-edit .edit-tool .edit-tool-scan:hover em {
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../common/images/icon/edit_scan_on.png');
}
.c-edit .edit-tool .edit-tool-save,
.c-edit .edit-tool .edit-tool-scan,
.c-edit .edit-tool .edit-tool-publish {
  float: right;
}
.c-edit .edit-tool .edit-tool-publish {
  width: 70px;
  height: 58px;
  line-height: 58px;
  text-align: center;
  font-size: 16px;
  color: #aeaeae;
  cursor: pointer;
}
.c-edit .edit-tool .edit-tool-publish .edit-tool-tip p {
  width: 118px;
}
.c-edit .edit-tool .edit-tool-tip {
  display: none;
  position: absolute;
  top: -36px;
  left: 50%;
  height: 36px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.c-edit .edit-tool .edit-tool-tip p {
  width: 48px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #aeaeae;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.8);
}
.c-edit .edit-tool .edit-tool-tip span {
  position: absolute;
  top: 25px;
  left: 50%;
  margin-left: -5px;
  display: inline-block;
  border: 4px solid;
  border-color: transparent rgba(0, 0, 0, 0.8) rgba(0, 0, 0, 0.8) transparent;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.c-edit .edit-content {
  height: 460px;
  overflow-y: auto;
  padding: 40px 60px;
  font-size: 16px;
  color: #aeaeae;
  background-color: #2d2d2d;
}
.c-edit .edit-content p,
.c-edit .edit-content div,
.c-edit .edit-content blockquote {
  line-height: 1.75rem;
  min-height: 16px;
  margin-bottom: 10px;
  font-size: 16px;
  color: #aeaeae;
  text-align: left;
}
.c-edit .edit-content img {
  max-width: 100%;
  margin-bottom: 10px;
  text-align: center;
}



