
.c-menu-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 220px;
}
.c-menu-left .item {
  position: relative;
  width: 100%;
  height: 60px;
}
.c-menu-left .item__title {
  display: block;
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 16px;
  color: #868686;
  cursor: pointer;
  text-decoration: none;
}
.c-menu-left .item__title:hover {
  color: #e1e1e1;
}
.c-menu-left .item__title.z-on {
  background-color: #181818;
  color: #e1e1e1;
  font-weight: bolder;
}
.c-menu-left.z-left .item__title {
  padding-left: 40px;
  text-align: left;
}
.c-menu-left.z-tip .item__tip {
  display: block;
}



