  margin: 0 auto;
}
.m-setting-backlist-content {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}
.m-setting-backlist-content .setting-right {
  width: 720px;
  margin-left: 240px;
}
.m-setting-backlist-content .setting-right .title {
  width: 100%;
  height: 60px;
  padding-left: 20px;
  padding-top: 22px;
  font-size: 16px;
  color: #505050;
  background-color: #373737;
}
.m-setting-backlist-content .setting-right .content {
  margin-bottom: 30px;
  background-color: #2d2d2d;
}
.m-setting-backlist-content .setting-right .content .content-wrap .c-author-list:last-child .c-author-list-wrap {
  border: none;
